.App {
  display:flex-box;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

table{
  display:flex;
  flex-direction: column;
  background-color:#61dafb;
}

.ToDoList{
  display:flex;
  flex-direction: column;
}

body{
  display:flex;
  justify-content: center;
}

.tmp{
  padding: 0 20px;
  display:flex;
  flex-direction: column;
}

.input{
  background-color: bisque;
}

.add-form-setting{
  background-color: rgb(255, 216, 169);
}

@media screen and (min-width:210px){
  /*210px以上で適用する内容*/
  table{
    display:flex;
    flex-direction: column;
    background-color:#d0f5ed;
  }
}
.post-item{
  border:solid 1px rgb(196, 196, 252);
  border-radius: 27%;	
  margin: 12px;
}

td{
  padding-right:10px;
  padding-left:10px

}



img{
  border-radius: 10%;
  margin: 0px 20px 10px 20px; 
  /* 上右下左 */

}

.post-item,.form-setting ,.add-form-setting,.table-setting{
  display:flex;
  flex-direction:column;
  align-items: center;
  justify-content: center;
}

.Post-setting {
  display:flex;
  flex-direction:row;
  align-items: center;
  justify-content: center;
}

.form-setting {
background-color: rgb(230, 241, 228);
}

button{
  margin-right:2px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
}

.modal-inner {
  display:flex;
  flex-direction:column;
  align-items: center;
  justify-content: center;
  
  position: absolute;
  top: 8%;
  right: 0;
  left: 0;
  bottom:8%;
  width: 80%;
  padding-bottom: 60px;
  margin: auto;
  background-color: rgb(255, 255, 255);
  border-radius: 8px;
}
