.modal-introduction  {
  color: #5876a3;
  width: 384px;
  line-height: 32px;
  text-align: left;
}

.modal-introduction,p, .modal-close-btn {
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;

}
.modal-close-btn {

  font-size: 13px;
  color: #8491a5;
  width: 200px;
  padding: 16px 0;
  border: 0;
  background-color: #f0f4f9;
  cursor: pointer;
}

.modal-close-btn:hover {
  color: #8491a5;
  background-color: #ccd9ea;
  transition: 0.3s ease-in-out;
}